import { useDispatch } from "react-redux";
import { configureStore } from "@reduxjs/toolkit";
import filterSlice from "./filter/slice";
import cartSlice from "./cart/slice";
import pizzasSlice from "./pizza/slice";

export const store = configureStore({
	reducer: {
		filterSlice,
		cartSlice,
		pizzasSlice,
	},
});

// —Ñ–Ω–∫—Ü–∏—è getState –≤—ã—Ç–∞—Å–∫–∏–≤–∞–µ—Ç –≤–µ—Å—å store, —ç—Ç–æ —Ñ—É–Ω–∫—Ü–∏—è —Å–æ –≤—Å–µ–º–∏ —Ç–∏–ø–∞–º–∏ –≤—Å–µ—Ö slice
// ReturnType –ø–æ–ª—É—á–∞–µ—Ç –ª—é–±—É—é —Ñ—É–Ω–∫—Ü–∏—é –∏ –µ—ë —Å–æ–¥–µ—Ä–∂–∏–º–æ–µ –ø—Ä–µ–≤—Ä–∞—â–∞–µ—Ç –≤ —Ç–∏–ø, —Ç.–µ. –∏–∑ —Ñ—É–Ω–∫—Ü–∏–∏ —Å —Ç–∏–ø–∞–º–∏ –≤—ã—Ç–∞—Å–∫–∏–≤–∞–µ—Ç —Ç–∏–ø—ã
// –≤ –∏—Ç–æ–≥–µ –º—ã –ø–æ–ª—É—á–∞–µ–º –≤—Å–µ —Ç–∏–ø—ã –∏–∑ store
// –¢–µ–ø–µ—Ä—å –≤–Ω—É—Ç—Ä–∏ RootState —Ö—Ä–∞–Ω—è—Ç—Å—è –≤—Å–µ —Ç–∏–ø—ã slice
export type RootState = ReturnType<typeof store.getState>;

export type AppDispatch = typeof store.dispatch;
export const useAppDispatch: () => AppDispatch = useDispatch;
