import { Routes, Route } from "react-router-dom";
import React, { Suspense } from "react";
import Loadable from "react-loadable"; // –¥–ª—è –Ω–µ–µ –æ—Ç–¥–µ–ª—å–Ω–æ —Å–∫–∞—á–∏–≤–∞—é—Ç—Å—è —Ç–∏–ø—ã install @types/react-loadable

import Home from "./pages/home";
//import Cart from "./pages/cart";
//import FullPizza from "./pages/fullPizza";
// import NotFound from "./pages/notFound";

import "./scss/app.scss";
import MainLayout from "./layouts/mainLayout";

// React Lazy –∫–æ–¥ —Å–ø–ª–∏—Ç–∏–Ω–≥(—Ä–∞–∑–¥–µ–ª–µ–Ω–∏–µ –∫–æ–¥–∞)–¥–ª—è –±—Ä–∞—É–∑–µ—Ä–∞ –∞ –†–µ–∞–∫—Ç Loadable –¥–ª—è —Å–µ—Ä–≤–µ—Ä–∞ –∏ –±—Ä–∞—É–∑–µ—Ä–∞
// Lazy –Ω–µ —Ä–∞–±–æ—Ç–∞–µ—Ç —Å –æ–±—ã—á–Ω—ã–º–∏ –∏–º–ø–æ—Ä—Ç–∞–º–∏ —Ç–æ–ª—å–∫–æ —Å default
const Cart = React.lazy(
	() => import(/* webpackChunkName:"Cart" */ "./pages/cart")
); // –†–µ–∞–∫—Ç Lazy –±—É–¥–µ—Ç –ø–æ–¥–≥—Ä—É–∂–∞—Ç—å —ç—Ç–æ—Ç –∫–æ–º–ø–æ–Ω–µ–Ω—Ç –æ—Ç–¥–µ–ª—å–Ω–æ –æ—Ç –≤—Å–µ–≥–æ –±–∞–Ω–¥–ª–∞, —Ç–æ–ª—å–∫–æ —Ç–æ–≥–¥–∞ –∫–æ–≥–¥–∞ –æ–Ω –±—É–¥–µ—Ç –Ω—É–∂–µ–Ω
const FullPizza = React.lazy(
	() => import(/* webpackChunkName:"FullPizza" */ "./pages/fullPizza")
);
//const NotFound = React.lazy(() => import(/* webpackChunkName:"NotFound" */ "./pages/notFound"));
const NotFound = Loadable({
	loader: () => import(/* webpackChunkName:"NotFound" */ "./pages/notFound"),
	loading: () => <div>–ò–¥–µ—Ç –∑–∞–≥—Ä—É–∑–∫–∞ —Å—Ç—Ä–∞–Ω–∏—Ü—ã...</div>,
});
const App = () => {
	return (
		<Routes>
			<Route path="/" element={<MainLayout />}>
				<Route path="/" element={<Home />} />
				<Route
					path="/cart"
					element={
						<Suspense fallback={<div>–ò–¥–µ—Ç –∑–∞–≥—Ä—É–∑–∫–∞ —Å—Ç—Ä–∞–Ω–∏—Ü—ã...</div>}>
							<Cart />
						</Suspense>
					}
				/>
				<Route
					path="/pizza/:id"
					element={
						<Suspense fallback={<div>–ò–¥–µ—Ç –∑–∞–≥—Ä—É–∑–∫–∞ —Å—Ç—Ä–∞–Ω–∏—Ü—ã...</div>}>
							<FullPizza />
						</Suspense>
					}
				/>
				<Route path="*" element={<NotFound />} />
			</Route>
		</Routes>
	);
};

export default App;
