import { createSlice, PayloadAction } from "@reduxjs/toolkit";
import { FilterSliceStateType, SortPropertyEnum, SortType } from "./type";

// –Ω–∞—á–∞–ª—å–Ω–æ–µ —Å–æ—Å—Ç–æ—è–Ω–∏–µ (state) –Ω–∞—à–µ–≥–æ —Ñ–∏–ª—å—Ç—Ä–∞
const initialState: FilterSliceStateType = {
	searchValue: "",
	categoryId: 0,
	currentPage: 1,
	sort: {
		name: "–ø–æ–ø—É–ª—è—Ä–Ω–æ—Å—Ç–∏",
		sortProperty: SortPropertyEnum.RATING_DESC,
	},
};

// –í filterSlice - createSlice —Å–æ–∑–¥–∞—Å—Ç slice –≥–¥–µ –±—É–¥–µ—Ç —Ö—Ä–∞–Ω–∏—Ç—Å—è –ª–æ–≥–∏–∫–∞ –æ–±—Ä–∞–±–æ—Ç–∫–∏ –¥–∞–Ω–Ω—ã—Ö –Ω–∞—à–µ–≥–æ state
const filterSlice = createSlice({
	// name –Ω—É–∂–µ–Ω —á—Ç–æ–± redux –º–æ–≥ –∫–æ—Ä—Ä–µ–∫—Ç–Ω–æ –∑–∞–¥–∞—Ç—å –æ–ø—Ä–µ–¥–µ–ª–µ–Ω–Ω—É—é –∫–æ–º–∞–Ω–¥—É
	name: "filter",
	initialState,
	reducers: {
		// –∏–∑–º–µ–Ω–µ–Ω–∏–µ –∫–∞—Ç–µ–≥–æ—Ä–∏–∏
		setCategoryId(state, action: PayloadAction<number>) {
			state.categoryId = action.payload;
		},
		// –∏–∑–º–µ–Ω–µ–Ω–∏–µ –≤ –ø–æ–ª–µ –ø–æ–∏—Å–∫–∞
		setSearchValue(state, action: PayloadAction<string>) {
			state.searchValue = action.payload;
		},
		// –∏–∑–º–µ–Ω–µ–Ω–∏–µ —Å–æ—Ä—Ç–∏—Ä–æ–≤–∫–∏
		setSort(state, action: PayloadAction<SortType>) {
			state.sort = action.payload;
		},
		// –∏–∑–º–µ–Ω–µ–Ω–∏–µ —Å—Ç—Ä–∞–Ω–∏—Ü—ã
		setCurrentPage(state, action: PayloadAction<number>) {
			state.currentPage = action.payload;
		},
		// –æ—Ç–ø—Ä–∞–≤–ª—è—é—Ç—Å—è –≤ qs, —á—Ç–æ–± —Å–æ—Ö—Ä–∞–Ω–∏—Ç—å —Å—Ç—Ä–∞–Ω–∏—Ü—É —Å –µ—ë —Ñ–∏–ª—å—Ç—Ä–∞–º–∏ (—Å—Ç—Ä–∞–Ω–∏—Ü–µ–π,—Å–æ—Ä—Ç–∏—Ä–æ–≤–∫–æ–π –∏ –∫–∞—Ç–µ–≥–æ—Ä–∏–µ–π)
		setFilters(state, action: PayloadAction<FilterSliceStateType>) {
			if (Object.keys(action.payload).length) {
				state.currentPage = Number(action.payload.currentPage);
				state.sort = action.payload.sort;
				state.categoryId = Number(action.payload.categoryId);
			} else {
				state.currentPage = 1;
				state.categoryId = 0;
				state.sort = {
					name: "–ø–æ–ø—É–ª—è—Ä–Ω–æ—Å—Ç–∏",
					sortProperty: SortPropertyEnum.RATING_DESC,
				};
			}
		},
	},
});

export const {
	setCategoryId,
	setSort,
	setCurrentPage,
	setFilters,
	setSearchValue,
} = filterSlice.actions;

export default filterSlice.reducer;
