import React, { useState } from "react";
import { useDispatch, useSelector } from "react-redux";
import { Link } from "react-router-dom";
import { addItem } from "../../redux/cart/slice";
import { selectCartItemById } from "../../redux/cart/selectors";

type PizzaBlockProps = {
	id: string;
	title: string;
	types: number[];
	price: number;
	imageUrl: string;
	sizes: number[];
};

const PizzaBlock: React.FC<PizzaBlockProps> = ({
	id,
	title,
	price,
	types,
	sizes,
	imageUrl,
}) => {
	const [activeType, setActiveType] = useState<number>(0); // –≤—ã–±–æ—Ä –ø–∏—Ü—Ü—ã —Ç–æ–Ω–∫–∞—è –∏–ª–∏ —Ç—Ä–∞–¥–∏—Ü–∏–æ–Ω–Ω–∞—è
	const [activeSize, setActiveSize] = useState<number>(0); //–≤—ã–±–æ—Ä —Ä–∞–∑–º–µ—Ä–∞–º –ø–∏—Ü—Ü—ã 26 30 40
	const pizzasType = ["—Ç–æ–Ω–∫–æ–µ", "—Ç—Ä–∞–¥–∏—Ü–∏–æ–Ω–Ω–æ–µ"];

	const dispatch = useDispatch();
	const cartItem = useSelector(selectCartItemById(id)); // –¥–æ–±–∞–≤–ª–µ–Ω–Ω–∞—è –ø–∏—Ü—Ü–∞ –≤ –∫–æ—Ä–∑–∏–Ω—É

	const addedCount = cartItem ? cartItem.count : 0; // –¥–æ–±–∞–≤–ª–µ–Ω–∏–µ —Å—á–µ—Ç—á–∏–∫–∞ –≤ –∫–Ω–æ–ø–∫—É –¥–æ–±–∞–≤–∏—Ç—å. –ü–æ —É–º–æ–ª—á–∞–Ω–∏—é –µ–≥–æ –Ω–µ—Ç

	const onClickAdd = () => {
		const item = {
			id,
			title,
			price,
			imageUrl,
			types: pizzasType[activeType],
			sizes: sizes[activeSize], //sizes –ø–æ–ª—É—á–∞–µ–º —Å –±—ç–∫–∞
			count: 0,
		};
		dispatch(addItem(item));
	};

	return (
		<div className="pizza-block-wrapper">
			<div className="pizza-block">
				<Link to={`pizza/${id}`}>
					<img className="pizza-block__image" src={imageUrl} alt="Pizza" />
					<h4 className="pizza-block__title">{title}</h4>
				</Link>

				<div className="pizza-block__selector">
					<ul>
						{types.map((type) => (
							<li
								onClick={() => {
									setActiveType(type);
								}}
								className={activeType === type ? "active" : ""}
								key={type}>
								{pizzasType[type]}
							</li>
						))}
					</ul>
					<ul>
						{sizes.map((size, i) => (
							<li
								onClick={() => {
									setActiveSize(i);
								}}
								className={activeSize === i ? "active" : ""}
								key={i}>
								{size} —Å–º.
							</li>
						))}
					</ul>
				</div>
				<div className="pizza-block__bottom">
					<div className="pizza-block__price">{price} ‚ÇΩ</div>
					<button
						className="button button--outline button--add"
						onClick={onClickAdd}>
						<svg
							width="12"
							height="12"
							viewBox="0 0 12 12"
							fill="none"
							xmlns="http://www.w3.org/2000/svg">
							<path
								d="M10.8 4.8H7.2V1.2C7.2 0.5373 6.6627 0 6 0C5.3373 0 4.8 0.5373 4.8 1.2V4.8H1.2C0.5373 4.8 0 5.3373 0 6C0 6.6627 0.5373 7.2 1.2 7.2H4.8V10.8C4.8 11.4627 5.3373 12 6 12C6.6627 12 7.2 11.4627 7.2 10.8V7.2H10.8C11.4627 7.2 12 6.6627 12 6C12 5.3373 11.4627 4.8 10.8 4.8Z"
								fill="white"
							/>
						</svg>
						<span>–î–æ–±–∞–≤–∏—Ç—å</span>
						{addedCount > 0 && <i>{addedCount}</i>}
					</button>
				</div>
			</div>
		</div>
	);
};

export default PizzaBlock;
