import { calcTotalPrice } from "./../../utils/calcTotalPrice";
import { getCartFromLS } from "./../../utils/getCartFromLS";
import { createSlice, PayloadAction } from "@reduxjs/toolkit";
import { CartItemType, CartSliceStateType } from "./type";

const { items, totalPrice } = getCartFromLS();

// –Ω–∞—á–∞–ª—å–Ω–æ–µ —Å–æ—Å—Ç–æ—è–Ω–∏–µ (state) –∏—Ç–æ–≥–æ–≤–æ–π —Ü–µ–Ω—ã –∏ –∫–æ–ª–ª–∏—á–µ—Å—Ç–≤–∞ —à—Ç –≤ –∫–æ—Ä–∑–∏–Ω–µ
const initialState: CartSliceStateType = {
	totalPrice,
	items,
};

// –í cartSlice - createSlice —Å–æ–∑–¥–∞—Å—Ç slice –≥–¥–µ –±—É–¥–µ—Ç —Ö—Ä–∞–Ω–∏—Ç—Å—è –ª–æ–≥–∏–∫–∞ –æ–±—Ä–∞–±–æ—Ç–∫–∏ –¥–∞–Ω–Ω—ã—Ö –Ω–∞—à–µ–≥–æ state
const cartSlice = createSlice({
	// name –Ω—É–∂–µ–Ω —á—Ç–æ–± redux –º–æ–≥ –∫–æ—Ä—Ä–µ–∫—Ç–Ω–æ –∑–∞–¥–∞—Ç—å –æ–ø—Ä–µ–¥–µ–ª–µ–Ω–Ω—É—é –∫–æ–º–∞–Ω–¥—É
	name: "cart",
	initialState,
	reducers: {
		// —É–≤–µ–ª–∏—á–∏–≤–∞–µ—Ç  –Ω–∞ 1 count –ø–∏—Ü—Ü—ã  –≤–Ω—É—Ç—Ä–∏ –∫–æ—Ä–∑–∏–Ω—ã
		addItem(state, action: PayloadAction<CartItemType>) {
			const findItem = state.items.find((obj) => obj.id === action.payload.id);
			if (findItem) {
				findItem.count++;
			} else {
				state.items.push({
					...action.payload,
					count: 1,
				});
			}
			state.totalPrice = calcTotalPrice(state.items);
		},

		// —É–º–µ–Ω—å—à–∞–µ—Ç  –Ω–∞ 1 count –ø–∏—Ü—Ü—ã  –≤–Ω—É—Ç—Ä–∏ –∫–æ—Ä–∑–∏–Ω—ã
		minusItem(state, action: PayloadAction<CartItemType>) {
			// —Ç–∏–ø string —Ç–∞–∫ –∫–∞–∫ –º—ã –ø–µ—Ä–µ–¥–∞–µ–º —Ç–æ–ª—å–∫–æ id
			const findItem = state.items.find((obj) => obj.id === action.payload.id);

			if (findItem && findItem.count > 0) {
				findItem.count--;
				state.totalPrice -= findItem.price;
			}
		},
		// —É–¥–∞–ª—è–µ—Ç –ø–∏—Ü—Ü—É  –∏–∑ –∫–æ—Ä–∑–∏–Ω—ã
		removeItem(state, action: PayloadAction<string>) {
			state.items = state.items.filter((obj) => obj.id !== action.payload);
		},
		//—É–¥–∞–ª—è–µ—Ç –≤—Å–µ –ø–∏—Ü—Ü—ã  –∏–∑ –∫–æ—Ä–∑–∏–Ω—ã
		clearItem(state) {
			state.items = [];
			state.totalPrice = 0;
		},
	},
});

// actions —ç—Ç–æ  reducers, –Ω–µ –∑–Ω–∞—é –∑–∞—á–µ–º –ø–µ—Ä–µ–∏–º–µ–Ω—É–µ—Ç—Å—è –∑–¥–µ—Å—å, –ê—Ä—á–∞–∫–æ–≤ —Å–∞–º –Ω–µ –∑–Ω–∞–µ—Ç
export const { addItem, removeItem, minusItem, clearItem } = cartSlice.actions;

export default cartSlice.reducer;
