import { RootState } from "./../store";
import { createSlice, createAsyncThunk, PayloadAction } from "@reduxjs/toolkit";
import axios from "axios";
import {
	FetchPizzasArgsType,
	PizzaSliceStateType,
	PizzaType,
	Status,
} from "./type";

// –Ω–∞—á–∞–ª—å–Ω–æ–µ —Å–æ—Å—Ç–æ—è–Ω–∏–µ (state) –ø–∏—Ü—Ü –∏ –∏—Ö —Å—Ç–∞—Ç—É—Å –ø–æ–ª—É—á–µ–Ω–∏—è
const initialState: PizzaSliceStateType = {
	items: [],
	status: Status.LOADING,
};

// Redux –Ω–µ —É–º–µ–µ—Ç —Ä–∞–±–æ—Ç–∞—Ç—å –≤ –∞—Å–∏–Ω—Ö—Ä–æ–Ω–Ω–æ–º —Ä–µ–∂–∏–º–µ –ø–æ—ç—Ç–æ–º—É –æ–±—Ä–∞–±–æ—Ç–∞—Ç—å –∑–∞–ø—Ä–æ—Å –Ω–∞ –ø–æ–º–æ–∂–µ—Ç createAsyncThunk
export const fetchPizzas = createAsyncThunk<PizzaType[], FetchPizzasArgsType>(
	"pizza/fetchPizzasStatus",
	// –º–æ–∂–Ω–æ –±—ã–ª–æ —Ç–∞–∫ —Å–¥–µ–ª–∞—Ç—å params:Record <string,string>
	async (params) => {
		const { sortBy, order, category, search, currentPage } = params;
		const { data } = await axios.get(
			`https://628baebb667aea3a3e34800b.mockapi.io/items?page=${currentPage}&limit=4&${category}&sortBy=${sortBy}&order=${order}&${search}`
		);
		return data; // –º–æ–∂–Ω–æ –±—ã–ª–æ –Ω–∞–ø–∏—Å–∞—Ç—å data:PizzaType[]
	}
);

// –í pizzasSlice - createSlice —Å–æ–∑–¥–∞—Å—Ç slice –≥–¥–µ –±—É–¥–µ—Ç —Ö—Ä–∞–Ω–∏—Ç—Å—è –ª–æ–≥–∏–∫–∞ –æ–±—Ä–∞–±–æ—Ç–∫–∏ –¥–∞–Ω–Ω—ã—Ö –Ω–∞—à–µ–≥–æ state
const pizzasSlice = createSlice({
	// name –Ω—É–∂–µ–Ω —á—Ç–æ–± redux –º–æ–≥ –∫–æ—Ä—Ä–µ–∫—Ç–Ω–æ –∑–∞–¥–∞—Ç—å –æ–ø—Ä–µ–¥–µ–ª–µ–Ω–Ω—É—é –∫–æ–º–∞–Ω–¥—É
	name: "pizza",
	initialState,
	reducers: {
		setItems(state, action: PayloadAction<PizzaType[]>) {
			state.items = action.payload;
		},
	},

	extraReducers: (builder) => {
		builder.addCase(fetchPizzas.pending, (state) => {
			state.status = Status.LOADING;
			state.items = [];
		});
		builder.addCase(fetchPizzas.fulfilled, (state, action) => {
			state.items = action.payload;
			state.status = Status.SUCCESS;
		});
		builder.addCase(fetchPizzas.rejected, (state) => {
			state.status = Status.SUCCESS;
			state.items = [];
		});
	},
});

export const { setItems } = pizzasSlice.actions;

export default pizzasSlice.reducer;
