import React, { memo, useEffect, useRef, useState } from "react";
import { useDispatch } from "react-redux/es/exports";
import { setSort } from "../redux/filter/slice";
import { SortPropertyEnum, SortType } from "../redux/filter/type";

type SortingProps = {
	value: SortType;
};

export const sortList: SortType[] = [
	// —Å –ø–æ–º–æ—â—å—é –º–∏–Ω—É—Å–∞ —Å–æ–∑–¥–∞–µ–º —É–Ω–∏–∫–∞–ª—å–Ω–æ—Å—Ç—å
	{ name: "–ø–æ–ø—É–ª—è—Ä–Ω–æ—Å—Ç–∏ (DESC)", sortProperty: SortPropertyEnum.RATING_DESC },
	{ name: "–ø–æ–ø—É–ª—è—Ä–Ω–æ—Å—Ç–∏ (ASC)", sortProperty: SortPropertyEnum.RATING_ASC },
	{ name: "—Ü–µ–Ω–µ (DESC)", sortProperty: SortPropertyEnum.PRICE_DESC },
	{ name: "—Ü–µ–Ω–µ (ASC)", sortProperty: SortPropertyEnum.PRICE_ASC },
	{ name: "–∞–ª—Ñ–∞–≤–∏—Ç—É (DESC)", sortProperty: SortPropertyEnum.TITLE_DESC },
	{ name: "–∞–ª—Ñ–∞–≤–∏—Ç—É (ASC)", sortProperty: SortPropertyEnum.TITLE_ASC },
];

const Sorting: React.FC<SortingProps> = memo(({ value }) => {
	const dispatch = useDispatch();

	const [open, setOpen] = useState(false); // –æ—Ç–∫—Ä—ã—Ç –ª–∏ popup –∏–ª–∏ –Ω–µ—Ç
	const sortRef = useRef<HTMLDivElement>(null);

	//–∑–∞–∫—Ä—ã—Ç–∏–µ popup –ø—Ä–∏ –∫–ª–∏–∫–µ –Ω–∞ –¥—Ä—É–≥—É—é –æ–±–ª–∞—Å—Ç—å
	useEffect(() => {
		const handleClickOutside = (event: MouseEvent) => {
			if (
				sortRef.current &&
				!event.composedPath().includes(sortRef.current) &&
				!open
			) {
				setOpen(false);
			}
		};
		document.body.addEventListener("click", handleClickOutside);
		return () => document.body.removeEventListener("click", handleClickOutside);
	}, []);

	// —Ñ—É–Ω–∫—Ü–∏—è –≤—ã–±–æ—Ä–∞ —Å–≤–æ–π—Å—Ç–≤–∞ —Å–æ—Ä—Ç–∏—Ä–æ–≤–∫–∏
	const handleSelected = (obj: SortType) => {
		console.log(obj);
		dispatch(setSort(obj));
		setOpen(false);
	};

	return (
		<div ref={sortRef} className="sort">
			<div className="sort__label">
				<svg
					width="10"
					height="6"
					viewBox="0 0 10 6"
					fill="none"
					xmlns="http://www.w3.org/2000/svg">
					<path
						d="M10 5C10 5.16927 9.93815 5.31576 9.81445 5.43945C9.69075 5.56315 9.54427 5.625 9.375 5.625H0.625C0.455729 5.625 0.309245 5.56315 0.185547 5.43945C0.061849 5.31576 0 5.16927 0 5C0 4.83073 0.061849 4.68424 0.185547 4.56055L4.56055 0.185547C4.68424 0.061849 4.83073 0 5 0C5.16927 0 5.31576 0.061849 5.43945 0.185547L9.81445 4.56055C9.93815 4.68424 10 4.83073 10 5Z"
						fill="#2C2C2C"
					/>
				</svg>
				<b>–°–æ—Ä—Ç–∏—Ä–æ–≤–∫–∞ –ø–æ:</b>
				<span onClick={() => setOpen(!open)}>{value.name}</span>
			</div>
			{open && (
				<div className="sort__popup">
					<ul>
						{sortList.map((obj, i) => (
							<li
								onClick={() => {
									handleSelected(obj);
								}}
								className={
									value.sortProperty === obj.sortProperty ? "active" : ""
								}
								key={i}>
								{obj.name}
							</li>
						))}
					</ul>
				</div>
			)}
		</div>
	);
});

export default Sorting;
