import React, { useCallback, useEffect, useRef } from "react";
import qs from "qs"; // –¥–ª—è —Å–æ—Ö—Ä–∞–Ω–µ–Ω–∏—è —Å—Å—ã–ª–æ–∫ –Ω–∞ —Å—Ç—Ä–∞–Ω–∏—Ü—É
import { useNavigate } from "react-router-dom";
import {
	setCategoryId,
	setCurrentPage,
	setFilters,
} from "../redux/filter/slice";
import { fetchPizzas } from "../redux/pizza/slice";
import {
	PizzaBlock,
	Categories,
	Skeleton,
	Pagination,
	Sorting,
} from "../components";
import { useAppDispatch } from "../redux/store";
import { useSelector } from "react-redux";
import { selectFilter } from "../redux/filter/selectors";
import { selectPizzas } from "../redux/pizza/selectors";
import { FetchPizzasArgsType } from "../redux/pizza/type";
import { sortList } from "../components/sorting";

const Home: React.FC = () => {
	const { categoryId, sort, currentPage, searchValue } =
		useSelector(selectFilter);
	const { items, status } = useSelector(selectPizzas);
	// useSelector –≤—ã—Ç–∞—Å–∫–∏–≤–∞–µ—Ç –¥–∞–Ω–Ω—ã–µ –∏–∑ —Ö—Ä–∞–Ω–∏–ª–∏—â–∞
	// useDispatch –≥–æ–≤–æ—Ä–∏—Ç —Å–¥–µ–ª–∞–π —á—Ç–æ-—Ç–æ
	const dispatch = useAppDispatch();
	const navigate = useNavigate();
	const urlParametr = useRef(false); // –¥–ª—è —Ç–æ–≥–æ —á—Ç–æ–±—ã –ø–æ–∫–∞–∑–∞—Ç—å –µ—Å—Ç—å –ª–∏ —Å–æ—Ö—Ä–∞–Ω–Ω–µ–Ω—ã–µ –ø–∞—Ä–∞–º–µ—Ç—Ä—ã —Å—Ç—Ä–∞–Ω–∏—Ü—ã –∏–ª–∏ –Ω–µ—Ç
	const firstRender = useRef(false); // –¥–ª—è —Ç–æ–≥–æ —á—Ç–æ–±—ã –∑–Ω–∞—Ç—å –∫–æ–≥–¥–∞ –±—ã–ª –ø–µ—Ä–≤—ã–π —Ä–µ–Ω–¥–µ—Ä —Å—Ç—Ä–∞–Ω–∏—Ü—ã

	// —Ñ—É–Ω–∫—Ü–∏—è –∏–∑–º–µ–Ω–µ–Ω–∏—è –∫–∞—Ç–µ–≥–æ—Ä–∏–∏, –æ–Ω–∞ –∫–∞–∂–¥—ã–π —Ä–∞–∑ –ø–µ—Ä–µ—Å–æ–∑–¥–∞–µ—Ç—Å—è –∫–æ–≥–¥–∞ –∫–æ–º–ø–æ–Ω–µ–Ω—Ç –¥–µ–ª–∞–µ—Ç –ø–µ—Ä–µ—Ä–∏—Å–æ–≤–∫—É
	const onChangeCategory = useCallback((idx: number) => {
		dispatch(setCategoryId(idx));
		dispatch(setCurrentPage(1));
	}, []);
	// —Ñ—É–Ω–∫—Ü–∏—è –∏–∑–º–µ–Ω–µ–Ω–∏—è –Ω–æ–º–µ—Ä–∞ —Å—Ç—Ä–∞–Ω–∏—Ü—ã
	const onPageNumberChange = (pageNumber: number) => {
		console.log(pageNumber);
		dispatch(setCurrentPage(pageNumber));
	};

	const getPizzas = async () => {
		// –¥–ª—è –Ω–∞—á–∞–ª–∞ –∫–æ—Ä—Ä–µ–∫—Ç–∏—Ä—É–µ–º –¥–∞–Ω–Ω—ã–µ
		const sortBy = sort.sortProperty.replace("-", "");
		const order = sort.sortProperty.includes("-") ? "asc" : "desc";
		const category = categoryId > 0 ? `category=${categoryId}` : "";
		const search = searchValue ? `search=${searchValue}` : "";
		//—Ç–µ–ø–µ—Ä—å —ç—Ç–∏ –¥–∞–Ω–Ω—ã–µ –≤—Å—Ç–∞–≤–ª—è–µ–º –≤ —Ñ—É–Ω–∫—Ü–∏—é
		dispatch(fetchPizzas({ sortBy, order, category, search, currentPage }));
		window.scrollTo(0, 0);
	};

	//–ï—Å–ª–∏ –∏–∑–º–µ–Ω–∏–ª–∏ –ø–∞—Ä–∞–º–µ—Ç—Ä—ã –∏ –±—ã–ª –ø–µ—Ä–≤—ã–π —Ä–µ–Ω–¥–µ—Ä, –≤—à–∏–≤–∞–µ—Ç –≤ –∞–¥—Ä–µ—Å–Ω—É—é —Å—Ç—Ä–æ—á–∫—É URL-–ø–∞—Ä–∞–º–µ—Ç—Ä—ã
	useEffect(() => {
		if (firstRender.current) {
			const queryString = qs.stringify({
				sortProperty: sort.sortProperty,
				categoryId,
				currentPage,
			});
			navigate(`/?${queryString}`);
		}
		firstRender.current = true;
	}, [categoryId, sort.sortProperty, searchValue, currentPage]);

	// –ï—Å–ª–∏ –Ω–µ—Ç URL-–ø–∞—Ä–∞–º–µ—Ç—Ä–æ–≤, –æ–±–Ω–æ–≤–ª—è–µ—Ç –¥–∞–Ω–Ω—ã–µ –ø—Ä–∏ –ø–µ—Ä–≤–æ–º —Ä–µ–Ω–¥–µ—Ä–µ –∏–ª–∏ –ø—Ä–∏ –∏–∑–º–µ–Ω–µ–Ω–∏–µ –ø–∞—Ä–∞–º–µ—Ç—Ä–æ–≤
	useEffect(() => {
		if (!urlParametr.current) {
			getPizzas();
		}
		urlParametr.current = false;
	}, [categoryId, sort.sortProperty, searchValue, currentPage]);

	// –ü—Ä–∏ –ø–µ—Ä–≤–æ–º —Ä–µ–Ω–¥–µ—Ä–µ –ø—Ä–æ–≤–µ—Ä—è–µ—Ç URL-–ø–∞—Ä–∞–º–µ—Ç—Ä—ã –∏ —Å–æ—Ö—Ä–∞–Ω—è–µ—Ç –≤ redux —Ç–æ–µ—Å—Ç—å –ø–∞—Ä—Å–∏—Ç
	useEffect(() => {
		if (window.location.search) {
			const params = qs.parse(
				window.location.search.substring(1)
			) as unknown as FetchPizzasArgsType;

			const sort = sortList.find((obj) => obj.sortProperty === params.sortBy);

			dispatch(
				setFilters({
					searchValue: params.search,
					categoryId: Number(params.category),
					currentPage: Number(params.currentPage),
					sort: sort || sortList[0],
				})
			);

			urlParametr.current = true;
		}
	}, []);

	const skeletons = [...new Array(6)].map((_, index) => (
		<Skeleton key={index} />
	));
	const pizzas = items.map((obj: any) => (
		<PizzaBlock {...obj} key={obj.id}></PizzaBlock>
	));

	return (
		<div className="container">
			<div className="content__top">
				<Categories value={categoryId} onChangeCategory={onChangeCategory} />
				<Sorting value={sort} />
			</div>
			<h2 className="content__title">–í—Å–µ –ø–∏—Ü—Ü—ã</h2>
			{status === "error" ? (
				<div className="content_error-info">
					<h2>–ü—Ä–æ–∏–∑–æ—à–ª–∞ –æ—à–∏–±–∫–∞ {/* <icon></icon> */}</h2>
					<p>
						–ö —Å–æ–∂–∞–ª–µ–Ω–∏—é –Ω–µ —É–¥–∞–ª–æ—Å—å –ø–æ–ª—É—á–∏—Ç—å –ø–∏—Ç—Å—ã. –ü–æ—Ä–æ–±—É–π—Ç–µ –ø–æ–≤—Ç–æ—Ä–∏—Ç—å –ø–æ–ø—ã—Ç–∫—É
						–ø–æ–∑–∂–µ
					</p>
				</div>
			) : (
				<div className="content__items">
					{status === "loading" ? skeletons : pizzas}
				</div>
			)}

			<Pagination
				currentPage={currentPage}
				onPageNumberChange={onPageNumberChange}
			/>
		</div>
	);
};

export default Home;
